import Vue from 'vue';
import { XHeader, Loading } from 'vux'
import Scroll from "./../../../components/scroll/scroll.vue";

export default {
  data() {
    return {
      TitleName: '',//
      TitleType: 0,//
      UncouponType: true,//
      Coupon_list: [], //
      CurrPage: 1, //
      PageSize: 20, //
      totalCount: 0, //
      PageCount: 0, //
      LoadObj: false, //
      hasMoreDataObj: false, //
      pullDownRefresh: false, //
      pullDownRefreshObj: {
        beforeTxt: "",
        releaseRefreshTxt: "",
        txt: "...",
        threshold: 40,
        stop: 0 //
      },
      pullUpLoad: true, //
      pullUpLoadObj: {
        threshold: -40,
        txt: { more: "", noMore: "" }
      },
      Key: this.getCookie("key"),//
      voucher_state: 1,
      noMore:'',//
      UsdCount: 0,//
      UsdingCount: 0,//
      ifhas:false,
    }
  },
  components: {
    XHeader,
    Loading,
    Scroll
  },
  name: 'coupon',
  //
  created: function () {
    if (this.Key == '') {
      this.$router.push('/login');
    }
    this.GetCouponCount(1);
    this.GetCouponCount(2);
    this.GetCouponList();
  },
  mounted() {

  },
  filters: {
      ChangeTime(value) {
          console.log(value)
          if (value != undefined) {
              value = value.replace('-', '.');
              value = value.replace('-', '.');

              var newdate = value;
              return newdate;
          }
      },
  },
  methods: {
    type_clic(type) {
      this.voucher_state = type;
    },//
    InitParams: function () {
      this.CurrPage = 1; //
      this.totalCount = 0; //
      this.PageCount = 0; //
      this.Coupon_list = []; //
      this.hasMoreDataObj = false; //
    },
    //
    onPullingDown: function () {
      this.InitParams();
      this.GetCouponCount(1);
      this.GetCouponCount(2);
      this.GetCouponList();
    },
    //
    onPullingUp: function () {
      // 
      if (this.hasMoreDataObj) {
        this.GetCouponList();
      }
    },
    //
    ChangeType: function (type) {
      if (type == '1' || type == '2') {
        this.TitleName = '';//
        this.TitleType = 0;//
        this.UncouponType = true;//
        this.ifhas = false;
      } else if (type == '3|4') {
        this.TitleName = '';//
        this.TitleType = 1;//
        this.UncouponType = false;//
        this.ifhas = true;
      }
      this.voucher_state = type;
      this.InitParams();
      this.GetCouponList();
    },
    //
    GetCouponList: function () {
      this.SearchLoad = true;
      // this.$axios.post('/member_voucher-voucher_list.html', this.$qs.stringify({
      //   curpage: this.CurrPage,
      //   page: this.PageSize,
      //   voucher_state: this.voucher_state,
      //   key: this.Key,
      // })
      this.$axios.get('/member_voucher-voucher_list.html', {
        params: {
          curpage: this.CurrPage,
          page: this.PageSize,
          voucher_state: this.voucher_state,
          key: this.Key,
        }
      }
      ).then((res) => {
        if (res.data.code == 200) {
          console.log(res.data.datas.voucher_list)
          res.data.datas.voucher_list.forEach(item => {
            if (item.voucher_state == 1) {
              this.$set(item, "couponclass", 'one');
            } else if (item.voucher_state == 2) {
              this.$set(item, "couponclass", 'ac');
            } else if (item.voucher_state == 3) {
              this.$set(item, "couponclass", 'over');
            } else if (item.voucher_state == 4) {
              this.$set(item, "couponclass", 'back');
            }
          })
          this.Coupon_list.push(...res.data.datas.voucher_list);
          console.log(res.data.datas);
          console.log(this.Coupon_list);
          // this.totalCount = res.data.datas.count;
          this.PageCount = res.data.page_total;
          this.CurrPage++; //
          this.hasMoreDataObj = res.data.hasmore;
          this.SearchLoad = false;
        }
      })
        .catch((err) => {
          this.SearchLoad = false;
          console.log(err);
        })
    },
    //
    GetCouponCount: function (value) {
      // this.$axios.post('/member_voucher-voucher_list.html', this.$qs.stringify({
      //   curpage: this.CurrPage,
      //   page: this.PageSize,
      //   voucher_state: value,
      //   key: this.Key,
      // })
      this.$axios.get('/member_voucher-voucher_list.html', {
        params: {
          curpage: 1,
          page: 1,
          voucher_state: value,
          key: this.Key,
        }
      }
      ).then((res) => {
        if (res.data.code == 200) {
          console.log(res.data.page_total);
          if (value == 1) {
            this.UsdCount = res.data.page_total;
          } else if (value == 2) {
            this.UsdingCount = res.data.page_total;
          }
        }
      })
        .catch((err) => {
          console.log(err);
        })
    },
    //
    GoBack: function () {
      if (this.TitleType == 0) {
        this.$router.go(-1);
      } else if (this.TitleType == 1) {
        this.TitleName = '';//
        this.TitleType = 0;//
        this.UncouponType = true;//
        this.voucher_state = 1;
        this.ifhas = false;
        this.InitParams();
        this.GetCouponList();
      }
    },
  },
}














