
import Vue from 'vue';
import Loading from 'vux/src/components/loading/index.vue'
import Alert from 'vux/src/components/alert/index.vue'

import Scroll from "./../../../components/scroll/scroll.vue";

export default {
components: {
Loading,
Alert,
Scroll,
},
name: 'barterzone',

data() {
return {
Good_list: [],//
CurrPage: 1, //
PageSize: 20, //
totalCount: 0, //
PageCount: 0, //
LoadObj: false,//
hasMoreDataObj: false,//
pullDownRefresh: false,//
pullDownRefreshObj: {
beforeTxt: "",
releaseRefreshTxt: "",
txt: "...",
threshold: 40,
stop: 0
},
pullUpLoad: true,//
pullUpLoadObj: {
threshold: -40,
txt: { more: "", noMore: "" }
},
SearchText: '',//()
fullHeight: 0,
SearchLoad: false,
}
},

created: function () {
if (this.$route.query.keyword != undefined) {
console.log('GetPageList');
this.SearchText = this.$route.query.keyword;
this.GetPageList();
} else {
console.log('GetGoodList');
this.GetGoodList();
}
},

mounted: function () {
this.fullHeight = this.autoHeight('conent');
console.log(this.fullHeight);
},

methods: {

InitParams: function () {
this.CurrPage = 1; 
this.totalCount = 0; 
this.PageCount = 0; 
this.Good_list = [];
},

onPullingDown: function () {
this.InitParams();
this.GetGoodList();
},

onPullingUp: function () {

if (this.hasMoreDataObj) {
this.GetGoodList();
}
},

GetGoodList: function () {
this.SearchLoad = true;
this.$axios.get('/goods-goods_list.html', {
params: {
curpage: this.CurrPage,
total_page: this.PageCount,
page: this.PageSize,
ex: 1,
}
}).then((res) => {
if (res.data.code == 200) {
this.Good_list.push(...res.data.datas.goods_list);
console.log(this.Good_list);
console.log(this.CurrPage);
this.totalCount = res.data.datas.count;
this.PageCount = res.data.datas.page_total;
this.CurrPage++; 
this.hasMoreDataObj = res.data.hasmore;
this.SearchLoad = false;
}
})
.catch((err) => {
this.SearchLoad = false;
console.log(err);
})
},

GetPageList: function () {
this.SearchLoad = true;
this.$axios.get('/goods-goods_list.html', {
params: {
curpage: this.CurrPage,
total_page: this.PageCount,
page: this.PageSize,
keyword: this.SearchText,
ex: 1,
}
}).then((res) => {
if (res.data.code == 200) {
this.Good_list.push(...res.data.datas.goods_list);
this.totalCount = res.data.datas.count;
this.PageCount = res.data.datas.page_total;
this.CurrPage++; 
this.hasMoreDataObj = res.data.hasmore;
this.LoadObj = true;
this.SearchLoad = false;
}
})
.catch((err) => {
this.SearchLoad = false;
console.log(err);
})
},
},
}


