import Vue from 'vue';
import { XInput, Group, XButton, XHeader, PopupHeader, Popup, Picker, Radio, TransferDom, Cell, Selector } from 'vux';
export default {
	components: {
		XInput,
		XButton,
		Group,
		XHeader,
		Picker, 
		PopupHeader, 
		Popup,
		Radio,
		Cell,
		Selector 
	},
	name: 'revisePho',
	directives: {
		TransferDom
	  },
	data() {
		return {
			img_code: '',//
			pho_code: '',//
			img_code_url_k: '',//urlkey
			img_code_url: null,//url
			err: '',
			express_cord:'',
			title: '',
			show1:false,
			value:'',
			left:'',
			list:[],
			return_id:0,
			right:'',
			title2:'',
			key:this.getCookie("key"),
			refund_id:this.$route.query.refund_id,
			time: 0,
			refund_sn:this.$route.query.refund_sn,
			express:[],//
			show: true,
			but_style: {
				background: '#dadada',
				height: '25px',
				lineHeight: '25px',
			},
			mobile: '',//
			pho_code_url: '',//url
			but_smb_url: ''//url
		}
	},
	//
	created: function () {
	  if (this.Key == '') {
		this.$router.push('/login');
	  }
	},
	//
	mounted: function () {
         this.getExpresslist();
	},
    change (value) {
				console.log('new Value', value)
	  },
	methods: {
		//
		getExpresslist(){
			this.$axios.get('/member_return-ship_form.html',{
				params:{
					key: this.key,
					refund_id: this.refund_id,
				} 
			 }).then((res) => {
			   if (res.data.code == 200) {
					 //this.list.push(res.data.datas.express_list);
					 this.list = [];
					 this.list.push(...res.data.datas.express_list);
					 console.log(this.list)
			   }
			 })
			   .catch((err) => {
				 console.log(err);
			   })
		},
		onChange (val) {
			console.log(val)
			return val;
		},
		//	
		next(){
			this.$axios.post('/member_return-ship_post.html', this.$qs.stringify({
				return_id: this.refund_id,
				key: this.key,
				express_id:this.value,
				invoice_no:this.express_cord

			})
			).then((res) => {
					this.$vux.alert.show({
						title: '',
						content: ''
					})
					if (res.data.code == 200) {
							this.SearchLoad = false;
							this.DelType = 1;
							this.DelNum--;
					}
			})
					.catch((err) => {
							this.SearchLoad = false;
							console.log(err);
					})
			}
	 }
	 
}


