import Vue from 'vue';
import { XHeader, Tab, TabItem, Swiper, SwiperItem, XButton, Cell, Loading} from 'vux'
import Scroll from "./../../../components/scroll/scroll.vue";
const list = () => ['', '', '', '', '']

export default {
    data () {
        return {
            list2: list(),
            demo2: '',
            index: 0,
            state_now:0,
            eventId:0,
            order_num:this.$route.query.order_state,
            codingState:'...',
            add_time:'',//
            logistics:"",
            stateing:0,
            Key: this.getCookie("key"),//
            //Key:'6c921998c5128a47e9ac436dc7f95b4f',
            State_type: 0,  // state_new:,state_payed:,state_send:,state_notakes:,state_noeval: , 
            Order_key: '', //()
            aftergoods_list: [],//
            CurrPage: 1, //
            PageSize: 20, //
            totalCount: 0, //
            PageCount: 0, //
            order_sn:'',//
            order_state:'',//
            LoadObj: false,//
            hasMoreDataObj: false,//
            pullDownRefresh: true,//
            pullDownRefreshObj: {
              beforeTxt: "",
              releaseRefreshTxt: "",
              txt: "...",
              threshold: 40,
              stop: 0//
            },
            pullUpLoad: true,//
            pullUpLoadObj: {
              threshold: -40,
              txt: { more: "", noMore: "" }
            },
            fullHeight: 0,
            SearchLoad: false,
        }
  },
  components: {
    XHeader,
    Tab,
    Cell,
    XButton,
    TabItem,
    Swiper,
    SwiperItem,
    Loading,
    Scroll
  },
  //
  created: function () {
    console.log(this.Key);
    if (this.Key == '') {
      this.$router.push('/login');
    }
  },
  mounted(){
    this.state_now = this.$route.params.state;
    this.fullHeight = this.autoHeight('conent');
    this.order_id = this.$route.query.order_id;
    console.log(this.fullHeight);

    this.GetMyaftergoods();
  },
  methods: {
    jumpTocodingdetail(){
      this.$router.push({ name: 'orderDetails'});
    },
    jumpTosalesdetail(value){
      this.$router.push({ name: 'applysalesDetail',query:{goods_id:value,order_id:this.order_id,order_state:this.order_state}}); 
    },
    //
    onPullingDown: function () {
      this.InitParams();
      this.GetMyaftergoods();
    },
    //
    onPullingUp: function () {
      // 
      if (this.hasMoreDataObj) {
        this.GetMyaftergoods();
      }
    },
    //
    GetMyaftergoods: function () {
      this.SearchLoad = true;
      this.$axios.get('/member_order-order_info.html',{
          params:{
            order_id: this.order_id,
            key: this.Key,
        }
      }).then((res) => {
        if (res.data.code == 200) {
          let orderMsg = res.data.datas.order_info;
          this.order_sn = orderMsg.order_sn;
          this.add_time = orderMsg.add_time;
          this.order_state = orderMsg.state_desc
          //state = orderMsg.state_desc
          // if(state == 20){
          //    this.order_state = '';
          // }else if(state == 30){
          //    this.order_state = '';
          // }else{
          //    this.order_state = '';
          // }
          this.aftergoods_list.push(...res.data.datas.order_info.goods_list);
          
          console.log(this.aftergoods_list);
          this.hasMoreDataObj = res.data.hasmore;
          // this.LoadObj = true;
          this.SearchLoad = false;
        }
      })
        .catch((err) => {
          this.SearchLoad = false;
          console.log(err);
        })
    },
   
  }, 
}














