import Vue from 'vue';
import { XInput, Group, XButton, Checker, XHeader } from 'vux';
export default {
  components: {
    XInput,
    XButton,
    Group,
    XHeader,
    Checker
  },
  name: 'register',
  data() {
    return {
      pho: '',//
      img_code: '',//
      pho_code: '',//
      agree: true,//
      code_text: "",
      img_code_url_k: '',//urlkey
      img_code_url: null,//url
      err: '',
      time: 0,//
      ivcode: '',//id
      //SSOID: '',
      but_style: {
        background: '#f16565',
        color: '#fff'
      }
    }
  },
  //
  mounted: function () {
    this.ivcode = this.$route.query.ivcode;
    //this.SSOID = this.getQueryString("SSOID");
    this.get_pic_code();
  },
  methods: {
    //
    get_pic_code() {
      this.$axios.get('/seccode-makecodekey.html', {}).then((res) => {
        if (res.data.code == "200") {
          this.img_code_url_k = res.data.datas.codekey;
          this.img_code_url = this.$globalScope.root + '/seccode-makecode.html?k=' + res.data.datas.codekey + '&t=' + Math.random();
        }
      }).catch((err) => {
        console.log(err);
      })
    },
    //
    img_code_cli() {
      this.get_pic_code();
    },
    //
    code_text_cli() {
      if (this.pho.search(/^1[34578]\d{9}$/) != -1) {
        this.$axios.get('/connect-get_sms_captcha.html', {
          params: { "type": 1, key: '', "sec_val": this.img_code, "sec_key": this.img_code_url_k, "phone": this.pho }
        }).then((res) => {
          if (res.data.state) {
            //
            this.but_style.background = "#ccc";
            this.$vux.alert.show({
              title: '',
              content: ''
            })
            clearInterval(this.interval);
            this.time = res.data.sms_time;
            this.code_text = this.time + "";
            this.interval = setInterval(() => {
              if (this.time > 0) {
                this.time--
                this.code_text = this.time + "";
              } else {
                this.code_text = "";
                this.but_style.background = "#f16565";
                clearInterval(this.interval);
              }
            }, 1000)
          }
          else {
            this.$vux.alert.show({
              title: '',
              content: res.data.msg
            })
          }
        }).catch((err) => {
          console.log(err);
        })
      }
      else {
        this.$vux.alert.show({
          title: '',
          content: ''
        })
      }
    },
    //
    register_subm() {
      if (this.pho.search(/^1[34578]\d{9}$/) != -1) {
        if (this.img_code != '' && this.img_code) {
          if (this.pho_code != '' && this.pho_code) {
            if (this.agree) {
              this.$axios.get('/connect-check_sms_captcha_login.html', {
                params: { "type": 1, "captcha": this.pho_code, "ivcode": this.ivcode, "phone": this.pho }
              }).then((res) => {
                if (res.data.code == "200") {
                  this.setCookie("key", res.data.datas.key);
                  this.setCookie("username", res.data.datas.username);
                  this.setCookie("userid", res.data.datas.userid);
                  this.$router.push('/my');
                }
                else {
                  this.$vux.alert.show({
                    title: '',
                    content: res.data.datas.error
                  })
                }
              }).catch((err) => {
                console.log(err);
              })
            } else {
              this.$vux.alert.show({
                title: '',
                content: ''
              })
            }
          } else {
            this.$vux.alert.show({
              title: '',
              content: ''
            })
          }
        } else {
          this.$vux.alert.show({
            title: '',
            content: ''
          })
        }
      } else {
        this.$vux.alert.show({
          title: '',
          content: ''
        })
      }
    },
    //
    wx_login() {
      location.href = this.$globalScope.root + '/connect-index.html';
    }
  }
}


