import Vue from 'vue';
import { XHeader, Loading } from 'vux'
import Scroll from "./../../../components/scroll/scroll.vue";

export default {
    data() {
        return {
            goodcoupon_list: [], //
            CurrPage: 1, //
            PageSize: 20, //
            totalCount: 0, //
            PageCount: 0, //
            LoadObj: false, //
            hasMoreDataObj: false, //
            pullDownRefresh: false, //
            pullDownRefreshObj: {
                beforeTxt: "",
                releaseRefreshTxt: "",
                txt: "...",
                threshold: 40,
                stop: 0 //
            },
            pullUpLoad: true, //
            pullUpLoadObj: {
                threshold: -40,
                txt: { more: "", noMore: "" }
            },
            Key: this.getCookie("key"),//
            voucher_state: 1,//
            voucher_store_id: this.$route.query.storeid,//id
            voucher_limit: this.$route.query.limit,//
            UsdCount: 0,//
            UsdingCount: 0,//
            Couponlist: [],//
        }
    },
    components: {
        XHeader,
        Loading,
        Scroll
    },
    name: 'goodcoupon',
    //
    created: function () {
        if (this.Key == '') {
            this.$router.push('/login');
        }
        console.log(this.voucher_store_id);
        console.log(this.voucher_limit);
        this.GetgoodcouponCount('1');
        this.GetgoodcouponCount('1|2|3|4');
        this.GetgoodcouponList();
    },
    mounted() {

    },
    filters: {
        ChangeTime(value) {
            // console.log(value)
            if (value != undefined) {
                value = value.replace('-', '.');
                value = value.replace('-', '.');

                var newdate = value;
                return newdate;
            }
        },
    },
    methods: {
        type_clic(type) {
            this.voucher_state = type;
        },//
        InitParams: function () {
            this.CurrPage = 1; //
            this.totalCount = 0; //
            this.PageCount = 0; //
            this.goodcoupon_list = []; //
            this.hasMoreDataObj = false; //
        },
        //
        onPullingDown: function () {
            this.InitParams();
            this.GetgoodcouponCount('1');
            this.GetgoodcouponCount('1|2|3|4');
            this.GetgoodcouponList();
        },
        //
        onPullingUp: function () {
            // 
            if (this.hasMoreDataObj) {
                this.GetgoodcouponList();
            }
        },
        //
        ChangeType: function (type) {
            this.voucher_state = type;
            this.InitParams();
            this.GetgoodcouponList();
        },
        //
        GetgoodcouponList: function () {
            this.SearchLoad = true;
            this.$axios.post('/member_voucher-voucher_choice_list.html', this.$qs.stringify({
                curpage: this.CurrPage,
                page: this.PageSize,
                voucher_state: this.voucher_state,
                voucher_store_id: this.voucher_store_id,
                voucher_limit: this.voucher_limit,
                key: this.Key,
            })
            ).then((res) => {
                if (res.data.code == 200) {
                    this.goodcoupon_list.push(...res.data.datas.voucher_list);
                    //   console.log(res.data.datas);
                    //   console.log(this.goodcoupon_list);
                    this.PageCount = res.data.page_total;
                    this.CurrPage++; //
                    this.hasMoreDataObj = res.data.hasmore;
                    this.SearchLoad = false;
                }
            })
                .catch((err) => {
                    this.SearchLoad = false;
                    console.log(err);
                })
        },
        //
        GetgoodcouponCount: function (value) {
            this.$axios.post('/member_voucher-voucher_choice_list.html', this.$qs.stringify({
                curpage: 1,
                page: 1,
                voucher_state: value,
                voucher_store_id: this.voucher_store_id,
                voucher_limit: this.voucher_limit,
                key: this.Key,
            })
            ).then((res) => {
                if (res.data.code == 200) {
                    //   console.log(res.data.page_total);
                    if (value == '1') {
                        this.UsdCount = res.data.datas.total;
                    } else if (value == '1|2|3|4') {
                        this.UsdingCount = res.data.datas.total;
                    }
                }
            })
                .catch((err) => {
                    console.log(err);
                })
        },
        //
        SetCoupon: function (storeid, voucherid, voucherprice) {
            console.log(this.voucher_state);
            if (this.voucher_state != '1|2|3|4') {
                var bool = false;
                var coupon = sessionStorage.getItem("coupon");
                if (coupon != '') {
                    this.Couponlist = JSON.parse(coupon);
                }
                if (this.Couponlist.length > 0) {
                    for (var i in this.Couponlist) {
                        if (this.Couponlist[i].split(',')[0] == storeid) {
                            this.Couponlist.splice(i, 1, storeid + ',' + voucherid + '|' + storeid + '|' + voucherprice);
                            bool = true;
                        }
                    }
                    if (!bool) {
                        this.$set(this.Couponlist, this.Couponlist.length, storeid + ',' + voucherid + '|' + storeid + '|' + voucherprice);
                    }
                } else {
                    this.$set(this.Couponlist, this.Couponlist.length, storeid + ',' + voucherid + '|' + storeid + '|' + voucherprice);
                }
                console.log(this.Couponlist);
                // console.log(JSON.stringify(this.Couponlist));
                sessionStorage.setItem('coupon', JSON.stringify(this.Couponlist));
                this.$router.go(-1);
                // var coupon = sessionStorage.getItem("coupon");
                // console.log(coupon);
                // var userEntity = JSON.parse(coupon);
                // console.log(userEntity);
            }
        },
    },
}














