import Vue from 'vue';
import { XHeader, Tab, TabItem, Swiper, SwiperItem, XButton, Loading } from 'vux'
import Scroll from "./../../../components/scroll/scroll.vue";

export default {
  data() {
    return {
      state: 1,
      orderNum: '5645216463554655685456',
      shopState: '',
      codingState: '2',
      seller_state: '',
      showNum: false,
      add_time: '2017-10-11 15:50',
      refund_amount: '632.0',
      Key: this.getCookie("key"),//
      JDState_type: '',//state_new,state_send,state_confirm,state_cancel
      TBState_type: '',//state_new,state_send,state_completed,state_close,state_choose
      Order_key: '',
      refund_list: [],//
      retrun_list: [],//
      CurrPage: 1, //
      PageSize: 20, //
      totalCount: 0, //
      PageCount: 0, //
      hasMoreDataObj: false,//
      pullDownRefresh: true,//
      pullDownRefreshObj: {
        beforeTxt: "",
        releaseRefreshTxt: "",
        txt: "...",
        threshold: 40,
        stop: 0//
      },
      pullUpLoad: true,//
      pullUpLoadObj: {
        threshold: -40,
        txt: { more: "...", noMore: "..." }
      },
      fullHeight: 0,
      SearchLoad: false,
    }
  },
  components: {
    XHeader,
    Tab,
    XButton,
    TabItem,
    Swiper,
    SwiperItem,
    Loading,
    Scroll
  },
  computed: {
  },
  //
  created: function () {
    if (this.Key == '') {
      this.$router.push('/login');
    }
    this.GetOrderList();
  },
  mounted() {
    this.fullHeight = this.autoHeight('conent');
    console.log(this.fullHeight);
  },
  methods: {
    jumpToreturndetail(value, state) {
      this.$router.push({ name: 'returnmoneyDetail', query: { refund_id: value, state: state } });
    },
    //
    ChangeType: function (type) {
      this.state = type;
      console.log(this.state)
      this.GetOrderList();
    },
    //
    InitParams: function () {
      this.CurrPage = 1; //
      this.totalCount = 0; //
      this.PageCount = 0; //
      this.Order_list = [];//
      this.hasMoreDataObj = false;//
    },
    //
    onPullingDown: function () {
      this.InitParams();
      this.GetOrderList();
    },
    //
    onPullingUp: function () {
      // 
      if (this.hasMoreDataObj) {
        this.GetOrderList();
      }
    },
    //
    GetOrderList() {
      if (this.state == 1) {
        this.GetrefundOrderList();
      } else if (this.state == 2) {
        this.GetRetrungoodsOrderList();
      }
    },
    //
    GetrefundOrderList: function () {
      this.SearchLoad = true;
      this.$axios.get('/member_refund-get_refund_list.html', {
        params: {
          curpage: this.CurrPage,
          page: this.PageSize,
          key: this.Key,
        }
      }).then((res) => {
        if (res.data.code == 200) {
          this.refund_list = [];
          this.refund_list.push(...res.data.datas.refund_list);
          this.totalCount = res.data.datas.count;
          this.PageCount = res.data.datas.page_total;
          this.CurrPage++; //
          this.hasMoreDataObj = res.data.hasmore;
          this.SearchLoad = false;
        }
      })
        .catch((err) => {
          this.SearchLoad = false;
          console.log(err);
        })
    },
    //
    GetRetrungoodsOrderList: function () {
      this.SearchLoad = true;
      this.$axios.get('/member_return-get_return_list.html', {
        params: {
          curpage: this.CurrPage,
          page: this.PageSize,
          key: this.Key,
        }
      }).then((res) => {
        if (res.data.code == 200) {
          this.retrun_list = [];
          this.retrun_list.push(...res.data.datas.return_list);
          console.log(this.retrun_list);
          this.totalCount = res.data.datas.count;
          this.PageCount = res.data.datas.page_total;
          this.CurrPage++; //
          this.hasMoreDataObj = res.data.hasmore;
          this.SearchLoad = false;
        }
      })
        .catch((err) => {
          this.SearchLoad = false;
          console.log(err);
        })
    },
  },
}














