export default {
    name: "App",
    data() {
        return {
            transitionName: ""
        };
    },
    watch: {
        $route(to, from) {
            //    console.log(':',to.path.split('/')[1],':',from.path.split('/')[1],':',this.transitionName)

            // const toDepth = to.path.split("/")[1].length;
            // const fromDepth = from.path.split("/")[1].length;
            this.transitionName = "slide-right";
        }
    }
};



