
import Vue from 'vue';
import Loading from 'vux/src/components/loading/index.vue'

import Scroll from "./../../../components/scroll/scroll.vue";

export default {
components: {
Loading,
Scroll
},
name: 'cusevaluation',

data() {
return {
Eval_list: [],//
Goods_evaluate_info: [],//
CurrPage: 1, //
PageSize: 20, //
totalCount: 0, //
PageCount: 0, //
LoadObj: false,//
hasMoreDataObj: false,//
pullDownRefresh: false,//
pullDownRefreshObj: {
beforeTxt: "",
releaseRefreshTxt: "",
txt: "...",
threshold: 40,
stop: 0
},
pullUpLoad: true,//
pullUpLoadObj: {
threshold: -40,
txt: { more: "", noMore: "" }
},
SearchLoad: false,
Goodsid: this.$route.query.goodsid,//ID
Type: '',//(0: 1: 2: 3: 4: 5:)
Contenttype: true,//(true:false:)
}
},

created: function () {
this.GetGoodsDetailList();
this.GetEvalList();
},

mounted: function () {
},
filters: {
ChangeTime(value) {
if (value.length == 10) {
value = value * 1000;
}

var date = new Date(value);

var Y = date.getFullYear() + '-';
var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
var D = date.getDate() + ' ';
var h = date.getHours() + ':';
var m = date.getMinutes() + ':';
var s = date.getSeconds();
var newdate = Y + M + D + h + m + s;
return newdate;
},
},

methods: {

InitParams: function () {
this.CurrPage = 1; 
this.totalCount = 0; 
this.PageCount = 0; 
this.Eval_list = [];
this.hasMoreDataObj = false;
},

onPullingDown: function () {
this.InitParams();
this.GetEvalList();
},

onPullingUp: function () {

if (this.hasMoreDataObj) {
this.GetEvalList();
}
},

ChangeType: function (type) {
this.Type = type;
this.InitParams();
this.GetEvalList();
},

GetEvalList: function () {
this.SearchLoad = true;
this.$axios.get('/goods-goods_evaluate.html', {
params: {
curpage: this.CurrPage,
total_page: this.PageCount,
page: this.PageSize,
goods_id: this.Goodsid,
type: this.Type,
}
}).then((res) => {
if (res.data.code == 200) {
this.Eval_list.push(...res.data.datas.goods_eval_list);
console.log(res.data.datas);
this.totalCount = res.data.datas.count;
this.PageCount = res.data.datas.page_total;
this.CurrPage++; 
this.hasMoreDataObj = res.data.hasmore;
this.LoadObj = false;
this.SearchLoad = false;
}
})
.catch((err) => {
this.SearchLoad = false;
console.log(err);
})
},

GetGoodsDetailList: function () {
this.SearchLoad = true;
this.$axios.get('/goods-goods_detail.html', {
params: {
goods_id: this.Goodsid,
key: '',
ivcode: 0,
}
}).then((res) => {
if (res.data.code == 200) {
this.Goods_evaluate_info = res.data.datas.goods_evaluate_info;
}
})
.catch((err) => {
console.log(err);
})
},
},
}


