import Vue from 'vue';
import { XHeader, Tab, TabItem, Swiper, SwiperItem, XButton, Loading } from 'vux'
import Scroll from "./../../../components/scroll/scroll.vue";

export default {
  data() {
    return {
      state: 1,
      order_state: 5,
      orderNum: '5645216463554655685456',
      shopState: '',
      codingState: '',
      codingTime: '2017-10-11',
      codingMoney: '632.0',
      Key: this.getCookie("key"),//
      State_type: '',//state_new,state_send,state_confirm,state_cancel
      Order_key: '',
      LoadObj: false,//
      Order_list: [],//
      jdOrder_list: [],//
      CurrPage: 1, //
      order_status: '',//
      jd_order_status: '',
      orderState: '',//
      jdorderState: '',//
      PageSize: 20, //
      totalCount: 0, //
      PageCount: 0, //
      hasMoreDataObj: false,//
      pullDownRefresh: true,//
      pullDownRefreshObj: {
        beforeTxt: "",
        releaseRefreshTxt: "",
        txt: "...",
        threshold: 40,
        stop: 0//
      },
      pullUpLoad: true,//
      pullUpLoadObj: {
        threshold: -40,
        txt: { more: "...", noMore: "..." }
      },
      fullHeight: 0,
      SearchLoad: false,
    }
  },
  components: {
    XHeader,
    Tab,
    XButton,
    TabItem,
    Swiper,
    SwiperItem,
    Loading,
    Scroll
  },
  //
  created: function () {
    if (this.Key == '') {
      this.$router.push('/login');
    }
    this.GetOrderList();
  },
  mounted() {
    this.fullHeight = this.autoHeight('conent');
    console.log(this.fullHeight);
  },
  filters: {
    //
    ChangeTime(value) {
      if (value.length == 10) {
        value = value * 1000;
      }
      // console.log(value);
      var date = new Date(value);
      // console.log(date);
      var Y = date.getFullYear() + '-';
      var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
      var D = date.getDate() + ' ';
      var h = date.getHours() + ':';
      var m = date.getMinutes() + ':';
      var s = date.getSeconds();
      var newdate = Y + M + D + h + m + s;
      return newdate;
    },

    //
    tbstate(value) {
      console.log(value)
      if (value == 1) {
        return ''
      } else if (value == 2) {
        return ''
      } else if (value == 3) {
        return ''
      } else if (value == 4) {
        return ''
      }
    },
    tbstate2(value) {
      if (value == 1) {
        return ''
      } else if (value == 2) {
        return ''
      } else if (value == 3) {
        return ''
      } else if (value == 4) {
        return ''
      } else {
        return ''
      }
    },

    //
    jdstate(value) {
      console.log(value)
      if (value == 1) {
        return ''
      } else if (value == 2) {
        return ''
      } else if (value == 0) {
        return ''
      } else if (value == 4) {
        return ''
      }
    },
    jdstate2(value) {
      if (value == 1) {
        return ''
      } else if (value == 2) {
        return ''
      } else if (value == 0) {
        return ''
      } else if (value == 4) {
        return ''
      } else {
        return ''
      }
    },
  },
  methods: {

    //
    InitParams: function () {
      this.CurrPage = 1; //
      this.totalCount = 0; //
      this.PageCount = 0; //
      this.Order_list = [];//
      this.hasMoreDataObj = false;//
    },
    //
    onPullingDown: function () {
      this.InitParams();
      this.GetOrderList();
    },
    //
    onPullingUp: function () {
      // 
      if (this.hasMoreDataObj) {
        this.GetOrderList();
      }
    },

    //
    ChangeType: function (type) {
      if (type == 5 || type == 10) {
        this.order_state = type;
      }
      console.log(this.order_state)
      if (this.order_state == 5) {
        if (type == 1) {
          this.State_type = 'state_send';
          this.state_now = 1;
        } else if (type == 2) {
          this.State_type = 'state_completed';
          this.state_now = 2;
        } else if (type == 3) {
          this.State_type = 'state_close';
          this.state_now = 3;
        } else {
          this.State_type = '';
          this.state_now = 0;
        }
      } else {
        if (type == 1) {
          this.State_type = 'state_send';
          this.state_now = 1;
        } else if (type == 2) {
          this.State_type = 'state_confirm';
          this.state_now = 2;
        } else if (type == 3) {
          this.State_type = 'state_cancel';
          this.state_now = 3;
        } else {
          this.State_type = '';
          this.state_now = 0;
        }
      }

      this.InitParams();
      this.GetOrderList();
    },
    //
    GetOrderList() {
      if (this.order_state == 5) {
        this.GetTBOrderList();
      } else if (this.order_state == 10) {
        this.GetJDOrderList();
      }
    },
    //
    GetJDOrderList: function () {
      this.SearchLoad = true;
      this.$axios.post('/member_order-jd_order_list.html?page=' + this.PageSize + '&curpage=' + this.CurrPage + '', this.$qs.stringify({
        state_type: this.State_type,
        key: this.Key,
      })
      ).then((res) => {
        if (res.data.code == 200) {
          this.jdOrder_list = [];
          this.jdOrder_list.push(...res.data.datas.order_group_list);
          this.totalCount = res.data.datas.count;
          this.PageCount = res.data.datas.page_total;
          this.CurrPage++; //
          this.hasMoreDataObj = res.data.hasmore;
          this.LoadObj = true;
          this.SearchLoad = false;
          // this.jdOrder_list.forEach(item => {
          //   console.log(item.order_status);
          //   if (item.order_status == 1) {
          //     this.order_status = ''
          //     this.jdorderState = ''
          //   } else if (item.order_status == 2) {
          //     this.order_status = ''
          //     this.jdorderState = ''
          //   } else if (item.order_status == 0) {
          //     this.order_status = ''
          //     this.jdorderState = ''
          //   } else if (item.order_status == 4) {
          //     this.order_status = ''
          //     this.jdorderState = ''
          //   } else {
          //     this.order_status = ''
          //   }
          // })
        }
      })
        .catch((err) => {
          this.SearchLoad = false;
          console.log(err);
        })
    },
    //
    GetTBOrderList: function () {
      this.SearchLoad = true;
      this.$axios.post('/member_order-taobao_order_list.html?page=' + this.PageSize + '&curpage=' + this.CurrPage + '', this.$qs.stringify({
        state_type: this.State_type,
        order_key: '',
        key: this.Key,
      })
      ).then((res) => {
        if (res.data.code == 200) {
          this.Order_list = [];
          this.Order_list.push(...res.data.datas.order_group_list);
          console.log(this.Order_list);
          // this.Order_list.forEach(item => {
          //   console.log("222" + item.order_status);
          //   console.log(this)
          //   if (item.order_status == 1) {
          //     this.order_status = ''
          //     this.orderState = ''
          //   } else if (item.order_status == 2) {
          //     this.order_status = ''
          //     this.orderState = ''
          //   } else if (item.order_status == 3) {
          //     this.order_status = ''
          //     this.orderState = ''
          //   } else if (item.order_status == 4) {
          //     this.order_status = ''
          //     this.orderState = ''
          //   } else {
          //     this.order_status = ''
          //   }
          // })

          this.totalCount = res.data.datas.count;
          this.PageCount = res.data.datas.page_total;
          this.CurrPage++; //
          this.hasMoreDataObj = res.data.hasmore;
          this.LoadObj = true;
          this.SearchLoad = false;
        }
      })
        .catch((err) => {
          this.SearchLoad = false;
          console.log(err);
        })
    },
    //
    jumpTosinglesupplement() {
      this.$router.push({ name: 'singleSupplement' });
    },
  },
}














