import Vue from 'vue';
import { XInput, Group, XButton, XHeader, XAddress, ChinaAddressV4Data } from 'vux';
export default {
  components: {
    XInput,
    XButton,
    Group,
    XHeader,
    XAddress
  },
  name: 'addAddress',
  data() {
    return {
      is_default: "0",//
      name: '',//
      pho: '',//
      province_id: '',//ID
      city_id: '',//ID
      county_id: '',//ID
      title: '',
      pageTitle: '',//
      button_text: '',//
      area_info: '',//-
      detailedAddress: '',//
      value: [],
      addressData: ChinaAddressV4Data,
      showAddress: false,
      Key: this.getCookie("key"),
    }
  },
  //
  created: function () {
    if (this.Key == '') {
      this.$router.push('/login');
    }
  },
  //
  mounted: function () {
    //
    if (this.$route.params.id) {
      this.button_text = "";
      this.pageTitle = "";
      this.$axios.post('/member_address-address_info.html', this.$qs.stringify({
        key: this.Key,//6c921998c5128a47e9ac436dc7f95b4f  this.getCookie("key")
        address_id: this.$route.params.id
      })
      ).then((res) => {
        if (res.data.code == 200) {
          this.pho = res.data.datas.address_info.mob_phone;
          this.name = res.data.datas.address_info.true_name;
          this.detailedAddress = res.data.datas.address_info.address;
          this.area_info = res.data.datas.address_info.area_info;
          this.province_id = res.data.datas.address_info.prov_id;
          this.city_id = res.data.datas.address_info.city_id;
          this.county_id = res.data.datas.address_info.area_id;
          this.$set(this.value, 0, this.province_id);
          this.$set(this.value, 1, this.city_id);
          this.$set(this.value, 2, this.county_id);
        } else {
          this.$vux.alert.show({
            title: '',
            content: res.data.datas.error
          })
        }
      })
        .catch((err) => {
          console.log(err);
        })
    }
    //
    else {
      this.button_text = "";
    }
  },
  methods: {
    //
    xAddressHide(str) {
      console.log(this.value);
      this.province_id = this.value[0];
      this.city_id = this.value[1];
      this.county_id = this.value[2];
      console.log(this.$refs.myAddress.nameValue);
      this.area_info = this.$refs.myAddress.nameValue
    },
    //
    xAddressShow() {
      //console.log('xAddressShow')
    },
    //
    xAddressChange(ids, names) {
    },
    //
    checkPho(val){
      if(!(/^[1][3,4,5,7,8][0-9]{9}$/.test(this.pho))){
        this.$vux.alert.show({
          title: '',
          content: ""
        })
      }
    },
    //
    submi() {
      var actionUrl = this.$route.params.id ? "/member_address-address_edit.html" : "/member_address-address_add.html";
      this.$axios.post(actionUrl, this.$qs.stringify({
        key: this.Key,//6c921998c5128a47e9ac436dc7f95b4f  this.getCookie("key")
        true_name: this.name,
        mob_phone: this.pho,
        city_id: this.city_id,
        area_id: this.county_id,
        address: this.detailedAddress,
        area_info: this.area_info,
        is_default: this.is_default,
        address_id: this.$route.params.id
      })
      ).then((res) => {
        if (res.data.code == 200) {
          this.$router.go(-1);
        } else {
          this.$vux.alert.show({
            title: '',
            content: res.data.datas.error
          })
        }
      })
        .catch((err) => {
          console.log(err);
        })
    },
  }
}  


