import Vue from 'vue';
import { XHeader, Loading} from 'vux'
import Scroll from "./../../../components/scroll/scroll.vue";

export default {
    data() {
        return {
            ifTrue: false,
            if_editor: '',
            isEditor: true,
            ifshow: false,
            agree: true,
            if_shopcar: true,
            ifdelete: false,
            ifHasgo: true,
            ifdisabled: false,
            promptMsg: '...',
            Key: this.getCookie("key"),//
            Favorites_list: [],//
            CheckFa_list: [],//
            CurrPage: 1, //
            PageSize: 20, //
            totalCount: 0, //
            PageCount: 0, //
            LoadObj: false,//
            hasMoreDataObj: false,//
            pullDownRefresh: true,//
            pullDownRefreshObj: {
                beforeTxt: "",
                releaseRefreshTxt: "",
                txt: "...",
                threshold: 40,
                stop: 0//
            },
            pullUpLoad: true,//
            pullUpLoadObj: {
                threshold: -40,
                txt: { more: "", noMore: "~" }
            },
            fullHeight: 0,
            SearchLoad: false,
            DelNum: 0,//
            DelType: 0,//01
        }
    },
    components: {
        XHeader,
        Loading,
        Scroll,
    },
    //
    created: function () {
        if (this.Key == '') {
            this.$router.push('/login');
        }
        this.GetFavoritesList();
    },
    mounted() {

    },
    watch: {
        DelNum: function (a, b) {
            // console.log("" + a);
            // console.log("" + b);
            if (this.DelType == 1) {
                if (a == 0) {
                    // this.$vux.alert.show({ content: '' });
                    this.$vux.toast.show({ text: '', type: 'text' });
                    this.InitParams();
                    this.GetFavoritesList();
                }
            }
        },
    },
    methods: {
        //
        InitParams: function () {
            this.CurrPage = 1; //
            this.totalCount = 0; //
            this.PageCount = 0; //
            this.Favorites_list = [];//
            this.hasMoreDataObj = false;//
        },
        //
        onPullingDown: function () {
            this.InitParams();
            this.GetFavoritesList();
        },
        //
        onPullingUp: function () {
            // 
            if (this.hasMoreDataObj) {
                this.GetFavoritesList();
            }
        },
        //
        GetFavoritesList: function () {
            this.SearchLoad = true;
            this.$axios.get('/member_favorites_store-favorites_list.html', {
                params: {
                    key: this.Key,
                    curpage: this.CurrPage,
                    // total_page: this.PageCount,
                    page: this.PageSize,
                }
            }).then((res) => {
                if (res.data.code == 200) {
                    this.Favorites_list.push(...res.data.datas.favorites_list);

                    this.Favorites_list.forEach(item => {
                        if (item.ifTrue != true) {
                            this.$set(item, "ifTrue", false);
                        }
                    })
                    console.log(this.Favorites_list);
                    this.totalCount = res.data.datas.count;
                    this.PageCount = res.data.datas.page_total;
                    this.CurrPage++; //
                    this.hasMoreDataObj = res.data.hasmore;
                    this.LoadObj = true;
                    this.SearchLoad = false;
                }
            })
                .catch((err) => {
                    this.SearchLoad = false;
                    console.log(err);
                })
        },
        //
        editor() {
            this.isEditor = !this.isEditor
            if (this.isEditor) {
                this.if_editor = "";
                this.ifshow = false;
                this.ifHasgo = true;
                this.if_shopcar = true;
                this.ifdelete = false;
            } else {
                this.if_editor = ""
                this.ifshow = true;
                this.if_shopcar = false;
                this.ifdelete = true;
                this.ifHasgo = false;
            }
        },
        //
        DelFavorites: function () {
            console.log(this.DelNum);
            if (this.DelNum > 0) {
                this.SearchLoad = true;
                this.Favorites_list.forEach(item => {
                    if (item.ifTrue == true) {
                        console.log(item.store_id);
                        this.$axios.post('/member_favorites_store-favorites_del.html', this.$qs.stringify({
                            store_id: item.store_id,
                            key: this.Key,
                        })
                        ).then((res) => {
                            if (res.data.code == 200) {
                                this.SearchLoad = false;
                                this.DelType = 1;
                                this.DelNum--;
                            }
                        })
                            .catch((err) => {
                                this.SearchLoad = false;
                                console.log(err);
                            })
                    }
                })
            } else {
                this.$vux.toast.show({ text: '', type: 'text' });
            }
        },
        //
        ClickOne: function (value) {
            if (this.Favorites_list[value].ifTrue == true) {
                this.$set(this.Favorites_list[value], "ifTrue", false);
                this.DelType = 0;
                this.DelNum--;
            } else if (this.Favorites_list[value].ifTrue == false) {
                this.$set(this.Favorites_list[value], "ifTrue", true);
                this.DelType = 0;
                this.DelNum++;
            }

            this.ifTrue = false;
            var ifTrueType = 0;
            this.Favorites_list.forEach(item => {
                if (item.ifTrue == true) {
                    ifTrueType++;
                }
            })
            if (ifTrueType == this.Favorites_list.length) {
                this.ifTrue = true;
            }

            // console.log('');
            // console.log(this.Favorites_list);
        },
        //
        ClickAll: function () {
            if (this.ifTrue == true) {
                this.ifTrue = false;
                this.DelNum = 0;
            } else if (this.ifTrue == false) {
                this.ifTrue = true;
                this.DelNum = this.Favorites_list.length;
            }
            this.Favorites_list.forEach(item => {
                this.$set(item, "ifTrue", this.ifTrue);
                if (this.ifTrue == true) {
                    this.DelType = 0;
                    // this.DelNum--;
                } else if (this.ifTrue == false) {
                    this.DelType = 0;
                    // this.DelNum++;
                }
            })

            // console.log('');
            // console.log(this.Favorites_list);
        },

    },
}














