import Vue from 'vue';
import { XHeader, Tab, TabItem, Swiper, SwiperItem, XButton, Loading, Popup, Search } from 'vux';
import Scroll from "./../../../components/scroll/scroll.vue";

export default {
  data() {
    return {
      demo2: '',
      index: 0,
      state_now: 0,
      eventId: 0,
      order_id: 0,//id
      Key: this.getCookie("key"),//
      //Key:'6c921998c5128a47e9ac436dc7f95b4f',
      ifconfirm:true,//
      State_type: 0,  // state_new:,state_payed:,state_send:,state_notakes:,state_noeval: , 
      Order_key: '', //()
      MyOrder_list: [],//
      CurrPage: 1, //
      PageSize: 20, //
      totalCount: 0, //
      PageCount: 0, //
      results: [],//
      value: 'test',//
      PayShow: false,//
      PayType: 0,//
      show_search:false,//
      PayText: '',
      Pay_sn: '',//
      Paylist: [],//
      PayName: 'online',//
      LoadObj: false,//
      hasMoreDataObj: false,//
      pullDownRefresh: true,//
      pullDownRefreshObj: {
        beforeTxt: "",
        releaseRefreshTxt: "",
        txt: "...",
        threshold: 40,
        stop: 0//
      },
      padding:{
        'padding-top':'40px'
      },
      pullUpLoad: true,//
      pullUpLoadObj: {
        threshold: -40,
        txt: { more: "", noMore: "" }
      },
      fullHeight: 0,
      SearchLoad: false,
    }
  },
  components: {
    XHeader,
    Tab,
    XButton,
    TabItem,
    Swiper,
    SwiperItem,
    Loading,
    Scroll,
    Popup,
    Search,
  },
  //
  created: function () {
    if (this.Key == '') {
      this.$router.push('/login');
    }
  },
  mounted() {
    this.state_now = this.$route.params.state;
    console.log(this.state_now);
    this.ChangeType(this.state_now);
    this.fullHeight = this.autoHeight('conent');
    console.log(this.fullHeight);
    this.GetMyOrderList();
  },
  methods: {
    jumpTocodingdetail(value,value2) {
      this.$router.push({ path: '/orderDetails',query:{order_id:value,pay_sn:value2} });
    },
    //
    InitParams: function () {
      this.CurrPage = 1; //
      this.totalCount = 0; //
      this.PageCount = 0; //
      this.MyOrder_list = [];//
      this.hasMoreDataObj = false;//
    },
    //
    onPullingDown: function () {
      this.InitParams();
      this.GetMyOrderList();
    },
    //
    onPullingUp: function () {
      // 
      if (this.hasMoreDataObj) {
        this.GetMyOrderList();
      }
    },
    jumpTologist(value){
      this.$router.push({ name: 'express', query:{order_id:value} });
    },
    //
    setFocus () {
      this.$refs.search.setFocus()
    },
    resultClick (item) {
      window.alert('you click the result item: ' + JSON.stringify(item))
    },
    getResult (val) {
      console.log('on-change', val)
      //this.results = val ? getResult(this.value) : []
    },
    onSubmit () {
      this.$refs.search.setBlur()
      // this.$vux.toast.show({
      //   type: 'text',
      //   position: 'top',
      //   text: 'on submit'
      // })
    },
    onFocus () {
      this.padding = {
        'padding-top':'40px'
      }
    },
    onCancel () {
      this.padding = {
        'padding-top':'40px'
      }
      this.show_search = false;
    },
    showSearch(){
       this.padding = {
         'padding-top':'0'
       }
       this.show_search = true;
    },
    //
    confirmGoods(value){
      this.$axios.post('/member_order-order_receive.html', this.$qs.stringify({
        order_id: value	,
        key: this.Key,
      })
      ).then((res) => {
        if (res.data.code == 200) {
          this.$vux.alert.show({ content: '' });
        }
      })
      .catch((err) => {
          console.log(err);
          this.$vux.alert.show({ content: err.data.datas.error });
      })
    },
    //
    ChangeType: function (type) {
      if (type == 1) {
        this.State_type = 'state_new';
        this.state_now = 1;
      } else if (type == 2) {
        this.State_type = 'state_payed';
        this.state_now = 2;
      } else if (type == 3) {
        this.State_type = 'state_send';
        this.state_now = 3;
      } else if (type == 4) {
        this.State_type = 'state_notakes';
        this.state_now = 4;
      } else {
        this.State_type = '';
        this.state_now = 0;
      }
      this.InitParams();
      this.GetMyOrderList();
    },
    //
    SetPayType:function(){
      this.PayShow = false;
      // console.log(this.$globalScope.root + 'member_payment-pay_new.html?key=' + this.Key + '&pay_sn='+this.Pay_sn+'&password=&rcb_pay=&pd_pay=&payment_code=' + this.PayType + '')
      window.location.href = this.$globalScope.root + 'member_payment-pay_new.html?key=' + this.Key + '&pay_sn='+this.Pay_sn+'&password=&rcb_pay=&pd_pay=&payment_code=' + this.PayType + '';
     },
     //
     ChangePayType: function (paytype) {
      this.PayType = paytype;
      // this.$vux.toast.show({ text: '' + paytype, type: 'text' });

      // window.location.href = this.$globalScope.root + '/member_payment-pay_new.html?key=' + this.Key + '&pay_sn='+this.Pay_sn+'&password=&rcb_pay=&pd_pay=&payment_code=' + this.PayType + '';
     },
     //
     ShowPay: function (value) {
       this.Pay_sn = value;
      this.$axios.post('/member_buy-pay.html', this.$qs.stringify({
          key: this.Key,//
          pay_sn: value,//
      })).then((res) => {
          if (res.data.code == 200) {
              console.log(res.data.datas);
              this.Paylist = res.data.datas.pay_info.payment_list;
              this.PayType = this.Paylist[0].payment_code;
              this.PayShow = true;
              // this.$vux.alert.show({ content: '' + res.data.datas.pay_sn });
          } else {
              this.$vux.toast.show({ text: res.data.datas.error, type: 'text' });
          }
      })
          .catch((err) => {
              console.log(err);
          })
    },
    //
    GetMyOrderList: function () {
      this.SearchLoad = true;
      this.$axios.post('/member_order-order_list.html?curpage=' + this.CurrPage + '&page=' + this.PageSize + '', this.$qs.stringify({
        // curpage: this.CurrPage,
        // total_page: this.PageCount,
        // page: this.PageSize,
        state_type: this.State_type,
        order_key: this.Order_key,
        key: this.Key,
      })
      ).then((res) => {
        if (res.data.code == 200) {
          this.MyOrder_list = [];
          this.MyOrder_list.push(...res.data.datas.order_group_list);
          console.log(this.MyOrder_list);
          this.totalCount = res.data.datas.count;
          this.PageCount = res.data.datas.page_total;
          this.CurrPage++; //
          this.hasMoreDataObj = res.data.hasmore;
          this.SearchLoad = false;

          // if(this.State_type = 'state_send' && this.MyOrder_list.order_list.refund_list != undefined){  
          //     this.ifconfirm = false;
          //     console.log(this.ifconfirm)
          //  }
        }
      }).catch((err) => {
          this.SearchLoad = false;
          console.log(err);
        })
    },
  },
}














