import Vue from 'vue';
import { XHeader, Tab, TabItem, Swiper, SwiperItem, XButton, Loading } from 'vux'

export default {
  data() {
    return {
      state: 1,
      title: '',
      orderNum: '5645216463554655685456',
      shopState: '',
      codingState: '2',
      theResult: '',
      showNum: false,
      refund_id: this.$route.query.refund_id,//ID
      state: this.$route.query.state,//or
      codingTime: '2017-10-11 15:50',
      codingMoney: '632.0',
      goods_img_360:'',//
      goods_name:'',//
      refund_sn:0,//
      reason_info:'',//
      seller_state:'',//
      admin_state:'',//
      seller_message:'',//
      refund_amount:'',//
      goods_num:0,//
      goods_spec:'',//
      show:false,
      showbtn:false,
      pic_list:[],//
      goods_img_360:'',//
      goods_name:'',//
      refund_sn:0,//
      reason_info:'',//
      seller_state:'',//
      admin_state:'',//
      seller_message:'',//
      refund_amount:'',//
      refund_num:0,//
      goods_spec:'',//
      show:false,
      showbtn:false,
      pic_list:[],//
      Key: this.getCookie("key"),//
      goods_img_360: '',//
      goods_name: '',//
      refund_sn: 0,//
      reason_info: '',//
      seller_state: '',//
      admin_state: '',//
      seller_message: '',//
      refund_amount: '',//
      refund_num: 0,//
      goods_spec: '',//
      show: false,
      showbtn: false,
      pic_list: [],//
    }
  },
  components: {
    XHeader,
    Tab,
    XButton,
    TabItem,
    Swiper,
    SwiperItem,
    Loading,
  },
  computed: {

  },
  //
  created: function () {
    if (this.Key == '') {
      this.$router.push('/login');
    }
    this.refundOrderDetail();
  },
  mounted() {
    this.fullHeight = this.autoHeight('conent');
    console.log(this.fullHeight);
    if (this.state == 1) {
      this.title = '';
      this.show = false;
      this.showbtn = false;
    } else {
      this.title = '';
      this.show = true;
      this.showbtn = true;
    }
  },
  methods: {
    //
    InitParams: function () {
    },
    //
    refundOrderDetail: function () {
      this.$axios.get('/member_refund-get_refund_info.html', {
        params: {
          key: this.Key,
          refund_id: this.refund_id
        }
      }).then((res) => {
        if (res.data.code == 200) {
           let refund = res.data.datas.refund;
           this.goods_img_360 = refund.goods_img_360;
           this.goods_name = refund.goods_name;
           this.goods_num = res.data.datas.goods[0].goods_num;
           console.log(this.goods_num);
           this.refund_sn = refund.refund_sn;
           this.reason_info = refund.reason_info;
           this.seller_state = refund.seller_state;
           this.admin_state = refund.admin_state;
           this.seller_message = refund.seller_message;
           this.refund_amount = refund.refund_amount;
           this.goods_spec = refund.goods[0].goods_spec;
           console.log(this.goods_spec);
           //this.refund_num = refund.refund_num;
           this.pic_list = res.data.datas.pic_list;

        }
      })
        .catch((err) => {
          console.log(err);
        })
    },

    //
    returnShipping(value) {
      this.$router.push({ name: 'singleDetails', query: { refund_id: this.refund_id, refund_sn: value } });
    }

  },
}














